<template>
    <div>
      <h2>{{ product.name }}</h2>
      <p>{{ product.description }}</p>
      <p> {{ product.price }}</p>
      <p>{{ product.category }}</p>
    </div>
  </template>

  <script>
  import axios from 'axios';

  export default {
    data() {
      return {
        product: {}
      };
    },
    mounted() {
      // Make a request to your PHP script to retrieve the details of the selected product
    //   axios.get('http://localhost/gpd.php', {
    //     params: {
    //       id: productId,
    //     },
    //  })
    const productt= this.$route.params.id;
    axios.get(`http://localhost/gpd.php/${productt}`,
    // {
    //     params: {
    //       id: productId,
    //     },
    // }
    )
      .then(response => {
        this.product = response.data;

        console.log(this.product)
      })
      .catch(error => {
        console.log(error);
      });
  }
}
</script>